/* /////////////////////////////////////////////////////////////////////////// */
/* <--------------------------- VIRTUAL ASSISTANT ---------------------------> */
/* /////////////////////////////////////////////////////////////////////////// */
.virtual-assistant-container {
  margin-top: 2rem;
  margin-bottom: 4rem;
}
.virtual-assistant {
  background-color: #fff;
  height: calc(1rem + 30vmin);
  width: calc(1rem + 30vmin);
  border-radius: 150px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
    inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
    inset -24px 0 30px #5dfcfc, 0 0 52px #fff, -16px 0 52px #f155e4,
    16px 0 52px #5dfcfc;
}

.paused {
  animation-play-state: paused;
  -webkit-animation-play-state: paused;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 50px #fff, -16px 0 50px #f155e4,
      16px 0 50px #5dfcfc;
  }
  25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #0ff, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #0ff, 0 0 60px #fff, -16px 0 60px #f155e4,
      16px 0 60px #0ff;
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 70px #fff, -16px 0 70px #f155e4,
      16px 0 70px #5dfcfc;
  }
  75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #0ff, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #0ff, 0 0 60px #fff, -16px 0 60px #f155e4,
      16px 0 60px #0ff;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 50px #fff, -16px 0 50px #f155e4,
      16px 0 50px #5dfcfc;
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 50px #fff, -16px 0 50px #f155e4,
      16px 0 50px #5dfcfc;
  }
  25% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #0ff, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #0ff, 0 0 60px #fff, -16px 0 60px #f155e4,
      16px 0 60px #0ff;
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 70px #fff, -16px 0 70px #f155e4,
      16px 0 70px #5dfcfc;
  }
  75% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #0ff, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #0ff, 0 0 60px #fff, -16px 0 60px #f155e4,
      16px 0 60px #0ff;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    box-shadow: inset 0 0 62px #fff, inset 24px 0 62px #f155e4,
      inset -24px 0 62px #5dfcfc, inset 24px 0 30px #f155e4,
      inset -24px 0 30px #5dfcfc, 0 0 50px #fff, -16px 0 50px #f155e4,
      16px 0 50px #5dfcfc;
  }
}
